body {
    font: 16px "Lato", Regular;
}

.header-container-operators {
    background-image: url("../../res/Fabric\ website\ header.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 342px;
    display: flex;
}

.header-title-operators {
    font: 60px "Lato", Bold;
    color: #FFFFFF;
    margin: auto;
    margin-left: 226px;
    margin-right: 0px;
    width: 655px;

}

.header-text-operators {
    font: 16px "Lato";
    font-weight: Regular;
    color: #FFFFFF;
    margin: auto;
    margin-left: 32px;
    margin-right: 32px;
    max-width: 307px;
}

@media screen and (max-width: 1052px) {
    .header-title-operators {
        margin-left: 32px;
    }
}
